@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  letter-spacing: 1px;
}

.App {
  background: radial-gradient(#ffffff, #ffefba);
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form {
  margin-top: 80px;
}

.form {
  display: flex;
  flex-direction: column;
  width: 600px;
  border: 2px solid steelblue;
  border-radius: 10px;
  padding: 40px;
  background-color: #f6f6f6;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.5);
}

.form input {
  padding: 10px;
  font-size: 1.1rem;
  border-radius: 10px;
  border: 2px solid black;
  outline: none;
}

label {
  margin-bottom: 20px;
  margin-top: 20px;
  font-size: 1.1rem;
}

.btn {
  margin-top: 40px;
  padding: 10px 20px;
  font-size: 1.1rem;
  background-color: steelblue;
  cursor: pointer;
  border: 2px solid black;
  border-radius: 10px;
}

.btn:hover {
  opacity: 0.9;
}

.modal-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
}

.inner-modal {
  width: 700px;
  height: 500px;
  background-color: #f6f6f6;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  border: 2px solid steelblue;
  border-radius: 10px;
}

.modal-title {
  font-size: 1.1rem;
  background-color: tomato;
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  margin-bottom: 20px;
}
.modal-message {
  font-size: 1.1rem;
}

.user-list {
  display: flex;
  flex-direction: column;
  width: 600px;
  border: 2px solid orangered;
  background-color: #f6f6f6;

  border-radius: 10px;
  padding: 40px;
  margin-top: 20px;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.5);
}

h2 {
  display: flex;
  justify-content: center;
}

.table-header th {
  padding: 10px;
}

table {
  border-collapse: separate;
  border-spacing: 0 15px;
}

small {
  font-size: 0.8rem;
  display: flex;
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 40px;
}

.user-name {
  display: flex;
  justify-content: center;
  padding: 10px;
}

.user-age {
  display: flex;
  justify-content: center;
}

.user {
  background-color: #fff;
  background-color: tomato;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 1.1rem;
}

.user:hover {
  opacity: 0.9;
}

.td-name {
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  border-top: 2px solid black;
  border-left: 2px solid black;
  border-bottom: 2px solid black;
}
.td-age {
  border-top: 2px solid black;
  border-right: 2px solid black;
  border-bottom: 2px solid black;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

